import {
  array, number,
  string, type, TypeOf,
} from 'io-ts';
import { StepSchema } from './Step';

const StepsPartSchema = type({
  id: number,
  name: string,
  steps: array(StepSchema),
}, 'StepsPartSchema');
type StepsPart = TypeOf<typeof StepsPartSchema>;

export type { StepsPart };
export { StepsPartSchema };
